body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    font-family: "SF UI Display", "Arial", sans-serif;
    color: #ffffff;
    text-decoration: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
  }
  
  a {
    text-decoration: none;
  }